@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
.App {
  height: 100%;
  text-align: center;
  padding: 0 20px 20px;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: #3b3b3b;
  background-color: #ffffff;
  background-image: url("data:image/svg+xml,%3Csvg width='6' height='6' viewBox='0 0 6 6' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='%2351bac9' fill-opacity='0.34' fill-rule='evenodd'%3E%3Cpath d='M5 0h1L0 6V5zM6 5v1H5z'/%3E%3C/g%3E%3C/svg%3E");
}

.title {
  font-size: 36px;
  font-weight: 700;
  padding: 30px;
  color: #505050;
  text-transform: uppercase;
}

.button {
  border: none;
  padding: 15px 15px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  outline: none;
  margin-bottom: 50px;
  -webkit-transition: -webkit-transform .3s;
  transition: -webkit-transform .3s;
  transition: transform .3s;
  transition: transform .3s, -webkit-transform .3s;
}

.button:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.button__sort {
  background-color: rgba(119, 221, 119, 0.5);
  margin-right: 20px;
}

.button__reset {
  background-color: rgba(240, 122, 93, 0.5);
}


.card {
    -webkit-transition: -webkit-transform 0.3s;
    transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
    transition: transform 0.3s, -webkit-transform 0.3s;
    padding: 20px 10px;
}

.card:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.card-text {
    padding-top: 20px;
    font-size: 20px;
    text-transform: uppercase;
}

.green_bg {
    background-color: rgba(119, 221, 119, 0.5);
}

.red_bg {
    background-color: rgba(240, 122, 93, 0.5);
}

.todo-list {
    list-style: none;
    padding: 0;
    display: grid;
    grid-gap: 20px;
    grid-template-columns: repeat(auto-fill, minmax(220px, 1fr) ) ;
}

